export const timeFormat = function (timestamp, flag) {
    if (!timestamp) return ''
    var date = new Date(timestamp);
    var Y = date.getFullYear();
    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1);
    var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
    var h = date.getHours();
    var m = date.getMinutes();
    var s = date.getSeconds();
    if (flag)
      return `${Y}-${M}-${D} ${h}:${m}:${s}`
    else
      return `${Y}-${M}-${D}`
  }

  export const browser = {
    versions: function () {
      var u = navigator.userAgent,
        app = navigator.appVersion;
      return { //
        trident: u.indexOf('Trident') > -1, //IE
        presto: u.indexOf('Presto') > -1, //opera
        webKit: u.indexOf('AppleWebKit') > -1, //
        gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //
        mobile: !!u.match(/AppleWebKit.*Mobile.*/), //
        ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
        android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
        iPhone: u.indexOf('iPhone') > -1, //iPhoneQQHD
        iPad: u.indexOf('iPad') > -1, //iPad
        webApp: u.indexOf('Safari') == -1 //web
      };
    }(),
    language: (navigator.browserLanguage || navigator.language).toLowerCase()
  }

  export const DivScroll = function (layerNode) {
    //ios
    //
    if (!document.querySelector(layerNode)) return;
    if (!browser.versions.ios) return
    console.log('')
    this.popupLayer = document.querySelector(layerNode);
    this.startX = 0;
    this.startY = 0;
  
    this.popupLayer.addEventListener(
      "touchstart",
      function (e) {
        this.startX = e.changedTouches[0].pageX;
        this.startY = e.changedTouches[0].pageY;
      },
      false
    );
  
    // innerScroll
    this.popupLayer.addEventListener(
      "touchmove",
      function (e) {
        e.stopPropagation();
        if (!browser.versions.ios) return
        var deltaX = e.changedTouches[0].pageX - this.startX;
        var deltaY = e.changedTouches[0].pageY - this.startY;
        // 
        if (Math.abs(deltaY) < Math.abs(deltaX)) {
          return false;
        }
  
        if (
          this.offsetHeight + this.scrollTop >=
          this.scrollHeight
        ) {
          if (deltaY < 0) {
            e.preventDefault();
            return false;
          }
        }
        if (this.scrollTop === 0) {
          if (deltaY > 0) {
            e.preventDefault();
            return false;
          }
        }
        // 
        // return false;
      },
      false
    );
    document.addEventListener(
      "touchmove",
      function (e) {
        // 
        if (e.cancelable) {
          // 
          if (!e.defaultPrevented) {
            e.preventDefault();
            e.stopPropagation();
          }
        }
      },
      false
    );
  };


